
import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import FlatButton from 'material-ui/FlatButton';
import Paper from 'material-ui/Paper';
import RaisedButton from 'material-ui/RaisedButton';

class Task extends Component {
  constructor(props, context) {
    super(props);
    this.state = {
      edit: false,
      counter: this.props.likes
    }
    
    this._editCard = this._editCard.bind(this)
    this._likeit   = this._likeit.bind(this)
  }

  _editCard() {
    this.setState({
      edit: true
    })
  }

  _remove(i) {
    this.props.remove(i);
  }

  _save(i, msg) {
    this.props.save(i, msg, this.state.counter)
    this.setState({
      edit: false
    })
  }

  _likeit(i) {
    this.setState({
      counter: this.state.counter + 1
    })
    this.props.savelikes(i, this.state.counter)
  }

  render() {
    if (!this.state.edit) {

      function show_counter(value) {
        return(
          <div className='quantity'>{value}</div>
        )
      }

      let heart, showc;

      if(this.state.counter == 0)
      {
        heart = 'likes'
      }
      else
      {
        heart = 'redlikes'
        showc = show_counter(this.state.counter)
      }

      return (
        <div className="cards">
          <MuiThemeProvider>
            <Paper
              className="paper"
              zDepth={5}
              rounded={false}
              children={this.props.children}
            />
          </MuiThemeProvider>
          <div className="removebutton">
            <MuiThemeProvider>
              <FlatButton label="X" secondary={true} fullWidth={true} onClick={(i) => this._remove(this.props.index)} />
            </MuiThemeProvider>
          </div>
          <div className="editbutton">
            <MuiThemeProvider>
              <FlatButton label="edit" primary={true} fullWidth={true} onClick={this._editCard} />
            </MuiThemeProvider>
          </div>
          {showc}
          <div className={heart} onClick={(i) => this._likeit(this.props.index)}></div>
        </div>
      )
    } else {
      return (
        <div className="cards">
          <MuiThemeProvider>
            <Paper
              className="paperEdit"
              zDepth={3}
              rounded={false}
              children={<textarea onChange={this.props.count} cols="21" rows="5" className="card-body-edit" ref="msg" defaultValue={this.props.children}></textarea>}
            />
          </MuiThemeProvider>
          <div className="savebutton">
            <MuiThemeProvider>
              <RaisedButton label="Save" primary={true} fullWidth={true} onClick={(i, msg) => this._save(this.props.index, this.refs.msg.value)} />
            </MuiThemeProvider>
          </div>
        </div>
      )
    }
  }
}

export default Task;


