
import React, { Component } from 'react';
import axios from 'axios';
import Task from './Task.js'
import Paper from 'material-ui/Paper';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

class Noteboard extends Component {
  constructor(props, context) {
    super(props);
    this.state = {
      comments: [],
      search: ''
    };

    this.apiUrl = 'https://5b8d961a5722ac001431743e.mockapi.io/notes/notes/' 

    this._addCard    = this._addCard.bind(this);
    this._removeCard = this._removeCard.bind(this);
    this._updateCard = this._updateCard.bind(this);
    this._updateLikes = this._updateLikes.bind(this);
    this._handleInput = this._handleInput.bind(this);
  }

  componentDidMount(){
    // Make HTTP reques with Axios
    axios.get(this.apiUrl)
      .then((res) => {
        // Set state with result
        this.setState({comments:res.data});
      });
  }

  _removeCard(i) {
    const remainder = this.state.comments.filter((todo) => {
      if(todo.id !== i) return todo;
    });
    axios.delete(this.apiUrl + i + '/')
      .then((response) => {
         this.setState({comments: remainder});
      });
  }

  _countMax(e) {
    const msg = e.target.value
    const count = msg.length
    if(count > 75) 
    {
      alert("You are recommended to type only 75 characters!!!")
    }
  }

  _handleInput(e) {
    this.setState({
      search: e.target.value
    })
  }

  render() {
    let content = this.state.comments.filter(
      (note) => {
          return note.text.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1;
    })

    const listItems = content.map((item) =>
      <Task key={item.id} index={item.id} count={this._countMax} remove={this._removeCard} edit={this._editCard} save={this._updateCard} savelikes={this._updateLikes} likes={item.likes}>{item.text}</Task>
    )

    return (      
      <div>
        <div className="addCard">
          <button type="button" className="btn btn-primary btn-sm mybutton" data-toggle="modal" data-target=".bd-example-modal-sm">Add card</button>
          <div className="modal fade bd-example-modal-sm" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-sm">
              <div className="modal-content mymodal">
                <MuiThemeProvider>
                  <Paper
                    className="paperAdd"
                    zDepth={5}
                    rounded={false}
                    children={<textarea onChange={this._countMax} cols="21" rows="5" className="card-body" ref="mmsg"></textarea>}
                  />
                </MuiThemeProvider>
                <button type="button" className="btn btn-primary btn-sm mybutton" data-dismiss="modal" onClick={(i) => this._addCard(this.refs.mmsg.value)}>Add</button>
              </div>
            </div>
          </div>
        </div>
        <input placeholder="Filter..." className='inputtext' onChange = {this._handleInput} type='text'></input>
        {listItems}
      </div>
    )
  }

  _addCard(i) {
    if (i.length < 1) 
    {
        alert("You have to at least type one character!!!")
    }
    else 
    {
      axios.post(this.apiUrl, {text: i, likes: 0})
      .then((res) => {
        this.state.comments.push(res.data);
        this.setState({apoiments: this.state.data});
        this.refs.mmsg.value = ''
     });
    }
  }

  _updateCard(i, msg, numlikes) {
    if(msg.length < 1)
    {
      alert("The card should contain at least one character!")
    }
    else
    {
      axios.put(this.apiUrl + i, {text: msg, likes: numlikes})
      .then((res) => {
        this.state.comments.push(res.data);
        window.location.reload();
      });
    }
  }

  _updateLikes(i, numlikes) {
    axios.put(this.apiUrl + i, {likes: numlikes+1})
    .then((res) => {
      this.state.comments.push(res.data);
    });
  }
}

export default Noteboard;



